import React from 'react';


class MovieRow extends React.Component {

  viewMovie() {
    const url = "https://www.themoviedb.org/movie/" + this.props.movie.id
    window.location.href = url
  }

// each class has a function called render that you can override
  render() {
    return  <div>
      <div key={this.props.movie.id}>
          <div className="movie__item">

              <div className="movie__item_poster">
                <img alt="poster" width="120" src={this.props.movie.poster_src}/>
              </div>
              <div className="movie__item_info">

              <div className="movie__item_moviename">
                {this.props.movie.title}
              </div>

              <div className="movie__item_description">
                <p>{this.props.movie.overview}</p>
              </div>

              <div className="movie__item_button">
                <input type="button" value="View" onClick={this.viewMovie.bind(this)} />
              </div>

            </div>

            </div>
      </div>
    </div>
  }
}





export default MovieRow;



